    export default {
	    data() {
            return {
            	id: 0,
            	detail: {},
                modelType: 1,
                reasonRefusal: '',
	            model: false,
                iphone_x: false,
            }
	    },
	    onLoad(options) { this.$commonLoad.onload(options);
	    	let that = this;
            uni.getSystemInfo({
                success: function (res) {
                    if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                        that.iphone_x = true;
                    }
                }
            })
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.id = options.id;
            that.getDetail();
	    },
	    methods: {
	    	getDetail() {
	            this.$request({
	                url: this.$api.app_admin.review_detail_v2,
	                data: {
	                    key: 'community',
	                    user_id: this.id
	                }
	            }).then(response => {
                    this.$hideLoading();
                    uni.hideLoading();
	            	if (response.code === 0) {
                        this.detail = response.data;
	            	}
	            }).catch(() => {
                    this.$hideLoading();
                    uni.hideLoading();
                });
	    	},

            refuse() {
                this.modelType = 1;
                this.model = true;
            },
            by() {
                this.modelType = 2;
                this.model = true;
            },
            confirm() {
                if (this.modelType === 2) {
                    this.$request({
                        url: this.$api.app_admin.review_switch_v2,
                        method: 'post',
                        data: {
                            key: 'community',
                            status: 1,
                            user_id: this.detail.user_id
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            uni.showToast({
                                title: '通过申请',
                                duration: 1000
                            });
                            this.model = false;
                            setTimeout(function(){
                            	uni.navigateBack();
                            })
                        }
                    })
                } else if (this.modelType === 1) {
                    this.$request({
                        url: this.$api.app_admin.review_switch_v2,
                        method: 'post',
                        data: {
                            key: 'community',
                            status: 2,
                            reason: this.reasonRefusal,
                            user_id: this.detail.user_id
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            this.reasonRefusal = '';
                            this.model = false;
                            uni.showToast({
                                title: '拒绝申请',
                                icon: 'none',
                                duration: 1000
                            });
                            setTimeout(function(){
                            	uni.navigateBack();
                            })
                        }else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    });
                }
            },
            cancel() {
                this.model=false;
            }
	    }
    }
